body {
  margin: 0;
  background: #e7efec;
}
nav {
  display: flex;
  height: 90px;
  background: #1eb7ee;
  box-shadow: 0px 5px 10px 3px rgba(153,153,153,1);
  justify-content: space-between;
  align-items: center;
}
nav a {
  text-decoration: none;
  color: white;
  font-size: 24px;
  margin-right: 50px;
}
nav menu {
  margin: 0;
  display: flex;
}
.logo {
  height: 70px;
  margin-left: 90px;
}
input {
  border: none;

}
.Header {
  text-align: center;
}
.Header  h1 {
  font-size: 50px;
  font-weight: 100;
}
.Header input {
  height: 60px;
  width: 400px;
  margin: 0;
  font-size: 24px;
}
.Header button {
  height: 60px;
  width: 170px;
  border-radius: 0 50px 0 0;
  border: none;
  background: #1eb7ee;
  font-size: 16px;
  color: white;
  margin-top: 2px;
  transition: 0.3s;
}
.Header button:hover {
  transition: 0.3s;
  border-radius: 0 25px 0 0;
  cursor: pointer;
}
.HeaderTable button {
  border: none;
  height: 50px;
  width: 50%;
}
.HeaderTable {
  margin: auto;
  margin-top: 20px;
  width: 1000px;
}

.HeaderTable .buttonin, .HeaderTable .buttonout {
  border-radius: 40px 40px 0 0;
  cursor: pointer;
  outline: none;
}
.HeaderTable .buttonDirect {
  background: #1eb7ee;
  border-radius: 40px 40px 0 0;
}
.HeaderTable .buttonin {
  background: none;
}
.HeaderTable .buttonout {
  background: white;
}

.HeaderTable menu {
  padding: 10px;
  background: white;
  margin: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 18px;
}
.white {
  background: white;
}

.SelectDay {
  display: inline-block;
  width: 70px;
  cursor: pointer;
  border-bottom: 2px solid #1eb7ee;
  margin-left: 15px;
}
td {
  
  font-size: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.table {
  width: 100%;
  border-collapse: collapse
}
th {
  color: rgb(71, 70, 70);
}
table tr:hover {
  color: #1eb7ee;
}
